/*===== Color-checkbox css start =====*/
.checkbox-color {
  display: inline-block;
  margin-right: 20px;
  cursor: pointer;
}
.checkbox-color label {
  display: inline-block;
  position: relative;
  padding-left: 10px;
  line-height: 20px;
}
.checkbox-color label::before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 0;
  width: 20px;
  height: 20px;
  left: 0;
  right: 0;
  text-align: center;
  margin-left: -20px;
  border: 1px solid #ccc;
  border-radius: 0;
  background-color: #fff;
  transition: border 0.15s ease-in-out, color 0.15s ease-in-out;
}
.checkbox-color label::after {
  display: inline-block;
  position: absolute;
  width: 16px;
  height: 16px;
  left: -6px;
  top: 0;
  margin-left: -17px;
  padding-left: 3px;
  padding-top: 1px;
  font-size: 20px;
  color: #fff;
}
.checkbox-color input[type=checkbox] {
  opacity: 0;
}
.checkbox-color input[type=checkbox]:focus + label::before {
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}
.checkbox-color input[type=checkbox]:checked + label::after {
  font-family: "IcoFont";
  content: "";
}
.checkbox-color input[type=checkbox]:disabled + label {
  opacity: 0.65;
}
.checkbox-color input[type=checkbox]:disabled + label::before {
  background-color: #eee;
  cursor: not-allowed;
}
.checkbox-color.checkbox-circle label::before {
  border-radius: 50%;
}
.checkbox-color.checkbox-inline {
  margin-top: 0;
}

.checkbox-danger input[type=checkbox]:checked + label::after, .checkbox-info input[type=checkbox]:checked + label::after, .checkbox-primary input[type=checkbox]:checked + label::after, .checkbox-success input[type=checkbox]:checked + label::after, .checkbox-warning input[type=checkbox]:checked + label::after {
  color: #fff;
}

.checkbox-primary input[type=checkbox]:checked + label::before {
  background-color: #2196f3;
  border-color: #2196f3;
  background-color: #4680ff;
}

.checkbox-warning input[type=checkbox]:checked + label::before {
  background-color: #FFB64D;
}

.checkbox-default input[type=checkbox]:checked + label::before {
  background-color: #e0e0e0;
}

.checkbox-danger input[type=checkbox]:checked + label::before {
  background-color: #FC6180;
}

.checkbox-success input[type=checkbox]:checked + label::before {
